	module.exports = {"pathContext":{"docHtml":"<h1>E2E Tests</h1>\n<p>The directory contains E2E tests and test applications. The test assume that Argo CD services are installed into <code>argocd-e2e</code> namespace or cluster in current context. One throw-away\nnamespace <code>argocd-e2e***</code> is created prior to tests execute. The throw-away namespace is used as a target namespace for test applications.</p>\n<p>The <code>test/e2e/testdata</code> directory contains various Argo CD applications. Before test execution directory is copies into <code>/tmp/argocd-e2e***</code> temp directory and used in tests as a\nGit repository via file url: <code>file:///tmp/argocd-e2e***</code>.</p>\n<h2>Running Tests Locally</h2>\n<ol>\n<li>Start the e2e version <code>make start-e2e</code> </li>\n<li>Run the tests: <code>make test-e2e</code></li>\n</ol>\n<p>You can observe the tests by using the UI <a href=\"http://localhost:8080/applications\">http://localhost:8080/applications</a>.</p>\n<h2>CI Set-up</h2>\n<p>The tests are executed by Argo Workflow defined at <code>.argo-ci/ci.yaml</code>. CI job The builds an Argo CD image, deploy argo cd components into throw-away kubernetes cluster provisioned\nusing k3s and run e2e tests against it.</p>","docPath":"argo-cd/docs/developer-guide/test-e2e","proj":"argo-cd"}}

